@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
  font-size: 62.5%;

  //  
  &.css-scroll-fixed:not(.open-only-mob) {
    @include scroll-blocked; }

  //   ,    open-only-mob
  &.css-scroll-fixed.open-only-mob {
    @include media('<tab') {
      @include scroll-blocked; } } }

body {
  min-width: $layout-page-min-width;
  @include font-normal;
  @include font($font-size, $line-height);
  color: $text-color;
  background: $bg-color url(../../img/empty.png) no-repeat left top fixed; }

//    
//       
@if nth($layout-page-space, 2) != 0 {
  .layout-page-spaces {
    @include padding-horizontal(nth($layout-page-space, 2)); } }

//      
//       
.layout-page {
  max-width: $layout-page-max-width;
  box-sizing: content-box;
  @include push-auto; }

//     
@if nth($layout-page-space, 2) != 0 {
  .layout-text-spaces {
    @include padding-horizontal(nth($layout-page-space, 2)); } }

//       
.layout-text {
  max-width: $layout-text-width;
  box-sizing: content-box;
  @include push-auto; }

//      
.layout-flood {
  max-width: $layout-text-width;
  box-sizing: content-box;
  @include push-auto; }

//     
//  html, body    100% (  reset.sass)
body {
  display: flex;
  flex-direction: column; }

.main {
  flex-shrink: 0; }

.footer {
  margin-top: auto; }
